import React from "react";

import styles from "./CalculatorPad.module.css";

import { connect } from "react-redux";
import * as actions from "../../store/actions/padClick";

function CalculatorPad(props) {
  let style = {};

  if (props.padName === "AC") {
    style = { backgroundColor: "rgb(172, 57, 57)" };
  } else if (props.padName === "=") {
    style = { backgroundColor: "rgb(0, 68, 102)" };
  } else if (props.padName === "0") {
    style = { backgroundColor: "rgb(82, 80, 80)" };
  }

  return (
    <div
      id={props.padID}
      className="drum-pad"
      onClick={() => props.padonClickHandler(props.padName)}
      style={style}
    >
      <div className={styles.CalculatorPad}>
        <button>{props.padName}</button>
      </div>
    </div>
  );
}

const mapDispatchToProps = dispatch => {
  return {
    padonClickHandler: name => dispatch(actions.padClick(name))
  };
};

export default connect(
  null,
  mapDispatchToProps
)(CalculatorPad);
