import React from "react";

import CalculatorPad from "../CalculatorPad/CalculatorPad";

import "./CalculatorPadSection.css";

export default function CalculatorPadSection(props) {
  const PadsArray = props.Pads.map(Pad => {
    return (
      <CalculatorPad
        key={Pad.ID}
        padID={Pad.ID}
        padSRC={Pad.SRC}
        padName={Pad.Name}
        padonClickHandler={props.padonClickHandler}
      />
    );
  });

  return <div className="CalculatorPadSection">{PadsArray}</div>;
}
